// @import '../../styles/mixin.scss';

.GoodsList {
    & {
        margin-top: 10px;
        margin-left: 10px;
    }
    &__container {
        display: flex;
        flex-direction: column;
    }
    // &__head {
    //     height: 50px;

    //     display: flex;
    //     flex-direction: row;
    //     align-items: center;
    //     justify-content: space-between;
    //     margin-bottom: 10px;
    // }
    &__goodsContainer {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 10px;
    }
    // &__sortSelector {
    //     width: 300px;
    //     margin-left: 10px;
    // }
    // &__btnFilter {
    //     display: none;
    // }
    &__noData {
        color: var(--color-grey-text);
        font-size: 26px;

        text-align: center;
        width: 100%;
        margin-top: 20px;
    }
}

@media (max-width: 1100px) {
    .GoodsList {
        & {
            margin-left: 0;
        }
        // &__btnFilter {
        //     display: block;
        //     margin-right: 10px;
        // }
    }
}

@media (max-width: 820px) {
    .GoodsList {
        &__goodsContainer {
            grid-template-columns: 1fr 1fr;
        }
    }
}

@media (max-width: 550px) {
    .GoodsList {
        &__goodsContainer {
            grid-template-columns: 1fr;
        }
    }
}
