.GoodPage{max-width:1280px;margin:auto;padding:10px 10px}.FullGoodInfo__container{display:flex;gap:40px;height:520px;width:100%}.GoodPhoto{flex-shrink:0;flex-grow:0;height:100%;width:630px}.GoodPhoto__container{width:100%;height:100%;display:flex;flex-direction:row;gap:20px}.GoodPhoto__mainPhotoContainer{width:calc(100% - 80px);height:100%;background-color:var(--color-gray-5);border-radius:10px;overflow:hidden}.GoodPhoto__photo{width:100%;height:100%;object-fit:cover}.GoodPhoto__photoList{flex-shrink:0;width:80px;height:100%;display:inline-flex;flex-direction:column;gap:10px;overflow-y:auto}.GoodPhoto__photoList::-webkit-scrollbar{width:0}.GoodPhoto__listElement{width:80px;height:80px;background-color:var(--color-gray-5);border-radius:10px;overflow:hidden}.GoodPhoto__photoElement{width:100%;height:100%;cursor:pointer;object-fit:cover}.GoodPhoto__photoElement_active{border:2px solid var(--color-accent)}.GoodPhoto__gallaryContainer{display:none}.GoodDescription{height:100%;width:calc(100% - 670px);flex-shrink:1;flex-grow:1}.GoodDescription__container{width:100%;max-width:400px}.GoodDescription__name{font-size:32px;color:var(--color-light-text);margin-bottom:10px}.GoodDescription__brand{display:flex;align-items:center;justify-content:space-between;margin-bottom:10px}.GoodDescription__brandName{font-size:20px;color:var(--color-light-text)}.GoodDescription__brandLogo{width:40px;height:40px;object-fit:contain}.GoodDescription__price{margin-top:15px;margin-bottom:15px;display:flex;align-items:center;justify-content:space-between}.GoodDescription__priceRight{color:var(--color-light-text);font-size:14px}.GoodDescription__priceSecondary{font-size:16px;color:var(--color-grey-text);text-decoration:line-through}.GoodDescription__priceSecondary::after{content:"₽"}.GoodDescription__priceMain{font-size:26px;color:var(--color-light-text)}.GoodDescription__priceMain::after{content:"₽"}.GoodDescription__actionBar{display:flex;gap:10px}.GoodDescription__typeName{color:var(--color-grey-text)}.GoodDescription__brandTitle{color:var(--color-grey-text)}.GoodDescription__button{width:calc(100% - 62px)}@media(max-width: 1050px){.FullGoodInfo__container{height:400px}.GoodPhoto{width:500px}.GoodDescription{width:calc(100% - 540px)}}@media(max-width: 890px){.FullGoodInfo__container{display:inline-flex;flex-direction:column;gap:10px;height:auto}.image-gallery-image{width:350px !important;height:350px !important}.GoodPhoto{width:100%;height:min(350px,100%)}.GoodPhoto__container{width:100%;height:100%}.GoodPhoto__photo{display:none}.GoodPhoto__photoList{display:none}.GoodPhoto__mainPhotoContainer{width:100%;height:350px;display:flex;align-items:center;justify-content:center}.GoodPhoto__gallaryContainer{width:min(100%,350px);height:100%;display:flex}.GoodDescription{width:100%}.GoodDescription__container{max-width:100%}}