.GoodCard{height:480px;max-width:500px;min-width:250px;padding:20px;border:2px solid var(--color-border-1);border-radius:10px}.GoodCard__container{width:100%;height:100%;display:inline-flex;flex-direction:column;justify-content:space-between}.GoodCard__photo{width:100%;height:250px;object-fit:contain;background:var(--color-gray-5);border-radius:10px}.GoodCard__name{color:var(--color-light-text);font-size:16px;margin-bottom:4px}.GoodCard__price{display:flex;flex-direction:row;justify-content:space-between;align-items:center;gap:10px}.GoodCard__priceLeft{display:flex;flex-direction:row;align-items:center;gap:10px}.GoodCard__priceRight{color:var(--color-light-text);font-size:14px}.GoodCard__priceMain{color:var(--color-light-text);font-size:18px}.GoodCard__priceMain::after{content:"₽"}.GoodCard__priceSecond{color:var(--color-grey-text);font-size:16px;text-decoration:line-through}.GoodCard__priceSecond::after{content:"₽"}.GoodCard__type{color:var(--color-grey-text);font-size:14px}.GoodCard__line{display:flex;flex-direction:row;align-items:center;justify-content:space-between;margin-bottom:6px}.GoodCard__brand{color:var(--color-grey-text);font-size:14px}.GoodCard__btns{width:100%;display:flex;gap:10px}.GoodCard__btn{width:calc(100% - 52px - 10px)}.FullGoodCard{border:2px solid var(--color-border-1);border-radius:10px}.FullGoodCard__container{width:100%;height:100%;padding:15px;display:flex;flex-direction:row}.FullGoodCard__photoContainer{width:210px;height:210px;display:flex;align-items:center;justify-content:center;border-radius:10px;overflow:hidden;flex-shrink:0;flex-grow:0;margin-right:25px}.FullGoodCard__photo{width:100%;height:100%;object-fit:contain}.FullGoodCard__infoContainer{flex-shrink:1;flex-grow:1;display:flex;flex-direction:column;justify-content:space-between;gap:10px}.FullGoodCard__name{font-size:16px;color:var(--color-light-text)}.FullGoodCard__description{color:var(--color-grey-text);text-align:justify;overflow:hidden;text-overflow:ellipsis;display:-moz-box;-moz-box-orient:vertical;display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical;line-clamp:3;box-orient:vertical}.FullGoodCard__price{display:flex;flex-direction:row;justify-content:space-between;align-items:center;gap:10px}.FullGoodCard__priceLeft{display:flex;flex-direction:row;align-items:center;gap:10px}.FullGoodCard__priceMain{color:var(--color-light-text);font-size:16px}.FullGoodCard__priceMain::after{content:"₽"}.FullGoodCard__priceSecond{color:var(--color-grey-text);font-size:14px;text-decoration:line-through}.FullGoodCard__priceSecond::after{content:"₽"}.FullGoodCard__inputsContainer{display:flex;flex-direction:row;gap:16px}.FullGoodCard__info{display:flex;flex-direction:column;gap:10px}@media(max-width: 550px){.FullGoodCard__container{flex-direction:column}.FullGoodCard__photoContainer{width:100%;margin-bottom:10px}}